<template>
  <div class="data">
    <div class="table">
      <base-table
        :columns="columns"
        :data="addData"
        :showSummary="true"
        :summaryMethod="getSummaries">
      </base-table>
    </div>
    <div class="option">
      <el-button type="primary" plain>下载预算模板</el-button>
      <el-button type="primary" plain>上载预算模板</el-button>
      <el-button type="primary" plain @click="save">保存</el-button>
      <el-button type="primary" plain >校验</el-button>
      <el-button type="primary" plain @click="approval">提交审批</el-button>
    </div>
  </div>
</template>

<script>
import {mapState} from 'vuex'
const BaseTable = () => import('@/components/BaseTable')

export default {
  name: 'Add',
  components: {BaseTable},
  data () {
    return {
      columns: [
        {prop: 'subInstitution', label: '机构', width: 160},
        {prop: 'type', label: '预算类型', width: 90},
        {prop: 'channel', label: '预算渠道', width: 100},
        {prop: 'timeSlot', label: '预算时间段', width: 200},
        {prop: 'bore', label: '预算口径', width: 100},
        {prop: 'productType', label: '业务分类', width: 100},
        {prop: 'targetPremium', label: '目标保费 (万元)', width: 140, isEdit: true, type: 'input'},
        {prop: 'targetOperaCostRate', label: '目标经营成本率', width: 140, isEdit: true, type: 'input'},
        {prop: 'targetReimburseRate', label: '目标赔付率', width: 140, isEdit: true, type: 'input'},
        {prop: 'targetMarketCostRate', label: '目标市场费用率', width: 140, isEdit: true, type: 'input'},
        {prop: 'targetNonMarketCostRate', label: '目标非市场费用率', width: 150, isEdit: true, type: 'input'}
      ]
    }
  },
  computed: {
    ...mapState({
      tableHeader: state => state.app.tableHeader,
      addData: state => state.budget.addData
    })
  },
  methods: {
    save () {
      this.columns.forEach(item => {
        if (item.isEdit !== undefined) {
          item.isEdit = false
        }
      })
    },
    approval () {
      this.$router.push('/admin/budget/approval')
    },
    getSummaries ({ columns, data }) {
      const sums = []

      if (data.length > 0) {
        columns.forEach((column, index) => {
          if (index === 0) {
            sums[index] = '全国总计:'
            return
          } else if (index === 1) {
            sums[index] = data[0].type
            return
          } else if (index === 2) {
            sums[index] = data[0].channel
            return
          } else if (index === 3) {
            sums[index] = data[0].timeSlot
            return
          } else if (index === 4) {
            sums[index] = data[0].bore
            return
          } else if (index === 5) {
            sums[index] = data[0].productType
            return
          }
          const values = data.map(item => {
            if (index === 6) {
              return Number(item[column.property])
            } else {
              return Number(item[column.property].replace('%', ''))
            }
          })

          if (!values.every(value => isNaN(value))) {
            sums[index] = values.reduce((prev, curr) => {
              const value = Number(curr)
              if (!isNaN(value)) {
                return prev + curr
              } else {
                return prev
              }
            }, 0)
            if (index > 6) {
              sums[index] /= data.length
              sums[index] += '%'
            }
          } else {
            sums[index] = 'N/A'
          }
        })
      }
      return sums
    }
  }
}
</script>



